import { NgModule } from '@angular/core';
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { ResourceMetadataComponent } from './resourcemetadata.component';
import { LayoutModule } from '@progress/kendo-angular-layout';

import { GridModule } from '@progress/kendo-angular-grid';

import { UploadModule } from '@progress/kendo-angular-upload';
import { HttpClientModule } from '@angular/common/http';
import {BaseinfoComponent} from './baseinfo/baseinfo.component';
import { ReactiveFormsModule } from '@angular/forms';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { TextgridComponent } from './textgrid/textgrid.component';
import { TextResourcesService } from './textgrid/textresources.service';
import { BitresourcesService } from './birgrid/bitresources.service';
import { BirgridComponent } from './birgrid/birgrid.component';
@NgModule({
  declarations: [
    ResourceMetadataComponent,
    BaseinfoComponent,
    TextgridComponent,
    BirgridComponent

  ],
  imports: [
    CommonModule,
    ButtonsModule,
    LayoutModule,
    GridModule,
    UploadModule,
    HttpClientModule,
    FormsModule 
  ],
  providers: [TextResourcesService,BitresourcesService],
  bootstrap: [ResourceMetadataComponent]
})
export class ResourceMetadataModule { }



