import { Component, OnInit, OnDestroy, ViewChild, Renderer2, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { GridComponent } from '@progress/kendo-angular-grid';
import { groupBy, GroupDescriptor } from '@progress/kendo-data-query';
import { TextResourcesService } from './textresources.service';
import { ResourceMetadata, TextItem, TextResourceItem, ResourceType } from '../resourcemetadata/resourcemetadata';

const createFormGroup = dataItem => new FormGroup({
    'textId':new FormControl(dataItem.textId),
    'id': new FormControl(dataItem.id),
    'value': new FormControl(dataItem.value)
  });
  const matches = (el, selector) => (el.matches || el.msMatchesSelector).call(el, selector);

  @Component({
    selector: 'app-textgrid',
    templateUrl: './textgrid.component.html',
    styleUrls: ['./textgrid.component.css']
  })

export class TextgridComponent implements OnInit,OnDestroy{

    @ViewChild(GridComponent)
    private grid: GridComponent;
    public groups: GroupDescriptor[] = [];
    public gridData: any[];
    private checktextelement:boolean=true;
    public formGroup: FormGroup;
    private textresourceitem:TextResourceItem[]=[];
    private editedRowIndex: number;
    private docClickSubscription: any;
    private isNew: boolean;
    @Input() currentViewModel:ResourceMetadata ;
    @Output() change = new EventEmitter();
    constructor(public service: TextResourcesService, private renderer: Renderer2) {}
    
    textmetadatas:TextItem[]=[{
        textId:1,
        id:'',
        value:''
    }];  
    data:TextItem[]=[]; 
    ngOnInit(): void {
      
        if(this.currentViewModel.stringResources.length!=0){
            let igridData = this.currentViewModel.stringResources;
            for(var i=0;i<igridData.length;i++){
                this.data[i]={ textId:i+1, id:'', value:''};
                this.data[i].id = igridData[i].id;
                this.data[i].value = igridData[i].value;
            }
            this.gridData = this.data; 
        }
        else {
            this.gridData = this.textmetadatas;
            this.data=this.textmetadatas;
        }
      this.docClickSubscription = this.renderer.listen('document', 'click', this.onDocumentClick.bind(this));
    }
    public ngOnDestroy(): void {
        this.docClickSubscription();
        this.saveCurrentViewModel();
        this.change.emit(this.currentViewModel);

    }

    public addHandler(): void {
        this.closeEditor();

        this.formGroup = createFormGroup({
            'id': '',
            'value': ''         
        });
        this.isNew = true;
        this.grid.addRow(this.formGroup);
       
    }

    public removeHandler(dataItem) {
       this.service.remove(dataItem,this.data);
  }

    public cellClickHandler({ isEdited, dataItem, rowIndex }): void {
        if (isEdited || (this.formGroup && !this.formGroup.valid)) {
            return;
        }
        this.saveCurrent();
        this.formGroup = createFormGroup(dataItem);
        this.editedRowIndex = rowIndex;

        this.grid.editRow(rowIndex, this.formGroup);
    }

    public cancelHandler(): void {
        this.closeEditor();
    }

    public groupChange(groups: GroupDescriptor[]): void {
        debugger;
        this.groups = groups;
        this.gridData = groupBy(this.service.textresources(), this.groups);
    }

    private closeEditor(): void {
        this.grid.closeRow(this.editedRowIndex);

        this.isNew = false;
        this.editedRowIndex = undefined;
        this.formGroup = undefined;
    }

    private onDocumentClick(e: any): void {
        if (this.formGroup && this.formGroup.valid &&
            !matches(e.target, '#textGrid tbody *, #textGrid .k-grid-toolbar .k-button')) {
            this.saveCurrent();
        }
    }

    private saveCurrent(): void {
        if (this.formGroup) {
            this.service.save(this.formGroup.value, this.isNew,this.data);
            this.closeEditor();
        }
    }
    private saveCurrentViewModel() :void{
      let textitem = this.data;
      this.currentViewModel.stringResources = [];
      for(var i=0 ;i <textitem.length;i++){
        if(this.checkTextElement(textitem[i],this.currentViewModel)){
          this.textresourceitem[i]={id:'',value:'',resourceType:ResourceType.TextFile}
          this.textresourceitem[i].id = textitem[i].id;
          this.textresourceitem[i].value = textitem[i].value;
          this.currentViewModel.stringResources.push(this.textresourceitem[i]);
        }
        else{
          this.currentViewModel.stringResources.find(x=>(x.id==textitem[i].id)).value = textitem[i].value;
        }
      }
    }

    checkTextElement(element:TextItem,resourcecontent:ResourceMetadata):any{
        for(var i=0;i<resourcecontent.stringResources.length;i++){
          if(resourcecontent.stringResources[i].id==element.id){
            this.checktextelement=false;
            break;
          }
          else this.checktextelement=true;
        }
        return this.checktextelement;
    }
}


