import { GSP, Package } from '@farris/ide-devkit';
import {ComponentFactoryResolver, Injector, Type} from '@angular/core';
import { ResourceMetadataComponent } from './views/resourcemetadatadesigner/resourcemetadata.component';
import { ResourceMetadataPackageView } from './views';
import { MetadataDto } from "@gsp-lcm/metadata-selector";
export class ResourceMetadataPackage extends Package {
  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openRs(uri));

    // if (!this.packageView) {
    //   this.packageView = new SgDesignerPackageView(state, this.cfr, this.injector);
    //   this.gsp.workspace.addMainPanel({id: this.id,title:this.data.name, item: this.packageView.getElement()});
    
    //   const currentElement = this.packageView.getElement().instance as SgDesignerComponent;
    //   if (currentElement) {
    //     currentElement.metadataContent = this.data;
    //   }
    // }
    // this.workspace.activePackage(this);
  }

  openRs(uri) {
    if (uri.endsWith('.rs')) {
      const pkgView = new ResourceMetadataPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as ResourceMetadataComponent;
      currentElement.metadataUri = uri;
      
      return {
        getTitle: () => uri,
        getElement: () => pkgView.getElement()
      };
    }
  }
  /**
   * 新建元数据
   */
  private createMetadata(data: MetadataDto) {
    this.gsp.eventBus.post(ResourceMetadataPackage, this.id, 'Command:CreateMetadata', data);
  }

}



