import { Injectable } from '@angular/core';
import { BitItem } from '../resourcemetadata/resourcemetadata';

@Injectable()
export class BitresourcesService {

    bitmetadatas:BitItem[]=[{
        bitId:1,
        id:'',
        value:''
    }];  

    // private data: any[] = bitresources;
    // private counter: number = bitresources.length;

    public bitresources(): any[] {
        return this.bitmetadatas;
    }

    public remove(bitresource: any,data:any[]): void {
        const index = data.findIndex(obj => obj.bitId === bitresource.dataItem.bitId);
        data.splice(index, 1);
        for(let i = index;i<=data.length-1;i++){
            data[i].bitId--;
        }
    }

    public save(bitresource: any, isNew: boolean,data:any[]): void {
       
        if (isNew) {
          bitresource.bitId = data.length+1;
          data.push(bitresource);
        } else {
            Object.assign(
                data.find(obj => obj.bitId === bitresource.bitId),
                bitresource
            );
        }
    }
}


