import { ComponentFactoryResolver, Injector, NgModuleRef } from '@angular/core';
import { ResourceMetadataComponent } from './resourcemetadatadesigner/resourcemetadata.component';
import { PackageView } from '@farris/ide-devkit';
import { ResourceMetadataModule } from './resourcemetadatadesigner/resourcemetadata.module';
export class ResourceMetadataPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector){
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(ResourceMetadataComponent);
    const moduleRef = this.injector.get(ResourceMetadataModule) as ResourceMetadataModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
  

}



