import { Injectable } from '@angular/core';
import { TextItem} from '../resourcemetadata/resourcemetadata';

@Injectable()
export class TextResourcesService {
    textmetadatas:TextItem[]=[{
        textId:1,
        id:'',
        value:''
    }];  
    public textresources(): any[] {
        return this.textmetadatas;
    }

    public remove(textresource: any,data:any[]): void {
        const index = data.findIndex( obj=> obj.textId === textresource.dataItem.textId);
        data.splice(index, 1);
        for(let i = index;i<=data.length-1;i++){
            data[i].textId--;
        }
    }

    public save(textresource: any, isNew: boolean,data:any[]): void {
        
        if (isNew) {
          textresource.textId = data.length+1;
            data.push(textresource);
        } else {
            Object.assign(
                data.find(({ textId }) => textId === textresource.textId),
                textresource
            );
        }
    }
}


