import { Component,ViewChild } from '@angular/core';
import { ResourceMetadata, TextResourceItem, ResourceType, TextItem, BinaryResourceItem, BitItem } from './resourcemetadata/resourcemetadata';
import { MetadataDto, GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { isNullOrUndefined } from 'util';
//import { HttpUrlConst } from '../../../bedesigner-frame/views/bedesigner-frame/util/be-reference';
import {TextgridComponent} from './textgrid/textgrid.component';
import { BirgridComponent } from './birgrid/birgrid.component';
import { CacheService } from "@farris/ide-devkit/services";
@Component({
  selector: 'app-resourcemetadata',
  templateUrl: './resourcemetadata.component.html',
  styleUrls: ['./resourcemetadata.component.css']
})
export class ResourceMetadataComponent {
  
  constructor(private lcmService: GSPMetadataService,private cache:CacheService) {
    
  }
  @ViewChild(TextgridComponent) childtext:TextgridComponent;
  @ViewChild(BirgridComponent) childbit:BirgridComponent;
  textresourceitem:TextResourceItem[]=[];
  bitresourceitem:BinaryResourceItem[]=[];
  public resourcecontent:ResourceMetadata={
    code:'',
    name:'',
    bizObject:'',
    lastModifiedTime:'',
    stringResources:this.textresourceitem,
    imageResources:this.bitresourceitem
  };
  public resourcemetadataDto: MetadataDto={
    id: '',
    nameSpace: '',
    code: '',
    name: '',
    fileName: '',
    type: '',
    bizobjectID: '',
    relativePath: '',
    extendProperty: '',
    content: ''
  };
  public dto:MetadataDto;
  selectedResource = true; 
  checktextelement:boolean=true;
  checkbitelement:boolean=true;
  public resourcemodel:ResourceMetadata={
    code:'',
    name:'',
    bizObject:'',
    lastModifiedTime:'',
    stringResources:this.textresourceitem,
    imageResources:this.bitresourceitem
  };
  private _metadataUri: string;
  
  public resourceTypeList: string[] = []; //资源类型列表
  set metadataUri(metadataUri: string) {
    const filename  = metadataUri.substring(metadataUri.lastIndexOf('/')+1);
    const filepath = metadataUri.substring(1,metadataUri.lastIndexOf('/'));
    this.lcmService.LoadMetadata(filename,filepath).subscribe(data =>{
      this.dto = data;
      this.loadRs(this.dto);
    });
    
    // todo: 通过元数据服务获取元数据内容
    ///////
    //const name = metadataUri.slice(metadataUri.lastIndexOf('/'));
    //const dto = new MetadataDto('', '', '', name, '', 'rs', '', '', '', '{"Code":"","Name":"","BizObject":"","StringResources":[],"ImageResources":[],"LastModifiedTime":""}');
  }
textChange($event) {
    this.resourcemodel.stringResources = $event.stringResources;
}
bitChange($event) {
  this.resourcemodel.imageResources = $event.imageResources;
}

  onSelectText():void{
      this.selectedResource = true;
  }
  onSelectBiry():void{
      this.selectedResource =false; 
  }

  set metadataContent(metadataContent: any) {
    this.loadRs(metadataContent);
  }

  loadRs(dto: MetadataDto): any {
    if (isNullOrUndefined(dto) || isNullOrUndefined(dto.content)) {
      this.resourcemodel = null;
      this.resourcemodel = null;
      return;
    }
    
    let temp = JSON.parse(dto.content);
    this.GetCamelParse(temp);
    this.resourcemodel = temp;
    this.resourcemetadataDto = dto;
   }

  //json串解析
  private GetCamelParse(jsonObj: any) {
    for (let key in jsonObj) {
      if (typeof (jsonObj[key]) === "object") {
        this.GetCamelParse(jsonObj[key]);
      }
      let temp = jsonObj[key];
      delete (jsonObj[key]);
      jsonObj[this.ConvertToCamel(key)] = temp;
    }
  }

  //字符串转换为Camel
  private ConvertToCamel(input: string): string {
    if (input.length > 0) {
      let len = input.length;
      var strTemp = ""; //新字符串
      for (let i = 0; i < len; i++) {
        if (i == 0) {
          strTemp += input[i].toLowerCase(); //第一个
        }
        else {
          strTemp += input[i];
        }
      }
      return strTemp;
    }
  }
    //json串解析
    private GetUnCamelParse(jsonObj: any) {
      for (let key in jsonObj) {
        if (typeof (jsonObj[key]) === "object") {
          this.GetUnCamelParse(jsonObj[key]);
        }
        let temp = jsonObj[key];
        delete (jsonObj[key]);
        jsonObj[this.ConvertToUncamel(key)] = temp;
      }
    }
   //字符串反转换Camel
   private ConvertToUncamel(input: string): string {
    if (input.length > 0) {
      let len = input.length;
      var strTemp = ""; //新字符串
      for (let i = 0; i < len; i++) {
        if (i == 0) {
          strTemp += input[i].toUpperCase(); //第一个
        }
        else {
          strTemp += input[i];
        }
      }
      return strTemp;
    }
  }
  
  onSave(){
    let tmpone = JSON.parse(this.resourcemetadataDto.content);
    this.GetCamelParse(tmpone);
    this.resourcecontent = tmpone;
    
    if(!isNullOrUndefined(this.childtext)){
      this.resourcecontent.stringResources=[];
        var textitem = this.childtext.data;
        for(var i=0 ;i <textitem.length;i++){
          if(this.checkTextElement(textitem[i],this.resourcecontent)){
            this.textresourceitem[i]={id:'',value:'',resourceType:ResourceType.TextFile}
            this.textresourceitem[i].id = textitem[i].id;
            this.textresourceitem[i].value = textitem[i].value;
          };
         this.resourcecontent.stringResources.push(this.textresourceitem[i]);
        }
    }
    else{
      this.resourcecontent.stringResources = this.resourcemodel.stringResources ;
    }

    if(!isNullOrUndefined(this.childbit)){
        this.resourcecontent.imageResources=[];
        var bititem = this.childbit.data;
        for(var i=0 ;i <bititem.length;i++){
          if(this.checkBitElement(bititem[i],this.resourcecontent)){
            this.bitresourceitem[i]={id:'',value:'',resourceType:ResourceType.BinaryFile}
            this.bitresourceitem[i].id = bititem[i].id;
            this.bitresourceitem[i].value = bititem[i].value;
          };
          this.resourcecontent.imageResources.push(this.bitresourceitem[i]);
        };
      }
      else{
        this.resourcecontent.imageResources = this.resourcemodel.imageResources;
      }
    this.GetUnCamelParse(this.resourcecontent);
    let stringcontent = JSON.stringify(this.resourcecontent);
    this.resourcemetadataDto.content = stringcontent;
    const sessionId = this.cache.get('sessionId');
    this.lcmService.SaveMetadata(this.resourcemetadataDto,sessionId).subscribe(() => {
      alert('保存成功');
    },
      () => {
      alert('保存失败');
      }
      )
      this.GetCamelParse(this.resourcecontent);
  }

  checkTextElement(element:TextItem,resourcecontent:ResourceMetadata):any{
    
    for(var i=0;i<resourcecontent.stringResources.length;i++){
      if(resourcecontent.stringResources[i].id==element.id){
        this.checktextelement=false;
        break;
      }
      else this.checktextelement=true;
    }
    return this.checktextelement;
  }

  checkBitElement(element:BitItem,resourcecontent:ResourceMetadata):any{
    for(var i=0;i<resourcecontent.imageResources.length;i++){
      if(resourcecontent.imageResources[i].id==element.id){
        this.checkbitelement=false;
        break;
      }
      else this.checkbitelement=true;
    }
    return this.checkbitelement;
  }

  onLoad(){
    let results=document.getElementById("uploads"); 
    results.click();
  }

  fileUploadonselect(){
    var uploadsFile = <HTMLInputElement>document.getElementById("uploads");
    var file = uploadsFile.files[0];
    let self = this;
    let reader = new FileReader();
    reader.readAsBinaryString(file);

    reader.onload = function() {
      let resultInfo  = JSON.parse(this.result.toString());
      let contentInfo = JSON.parse(resultInfo.Content);
      self.GetCamelParse(contentInfo);
      self.resourcemodel.stringResources = contentInfo.stringResources;
      self.resourcemodel.imageResources = contentInfo.imageResources;
    }
    this.resourcemodel.stringResources = self.resourcemodel.stringResources;
    this.resourcemodel.imageResources = self.resourcemodel.imageResources;
  }
}





