
import { FormGroup, FormControl } from '@angular/forms';
import { Component, OnInit, OnDestroy, ViewChild, Renderer2,Input, Output, EventEmitter} from '@angular/core';
import { GridComponent } from '@progress/kendo-angular-grid';
import { groupBy, GroupDescriptor } from '@progress/kendo-data-query';
import { BitresourcesService } from './bitresources.service';
import { BitItem, ResourceMetadata, BinaryResourceItem, ResourceType } from '../resourcemetadata/resourcemetadata';
import { isNullOrUndefined } from 'util';


const createFormGroup = dataItem => new FormGroup({
  'bitId':new FormControl(dataItem.bitId),
  'id': new FormControl(dataItem.id),
  'value': new FormControl(dataItem.value)
});
const matches = (el, selector) => (el.matches || el.msMatchesSelector).call(el, selector);
@Component({
  selector: 'app-birgrid',
  templateUrl: './birgrid.component.html',
  styleUrls: ['./birgrid.component.css']
})
export class BirgridComponent implements OnInit,OnDestroy {

  @ViewChild(GridComponent)
  protected grid: GridComponent;
  public groups: GroupDescriptor[] = [];
  public gridData: any[];
  private Info :any;
  public formGroup: FormGroup;
  private bitresourceitem:BinaryResourceItem[]=[];
  private editedRowIndex: number;
  private docClickSubscription: any;
  private isNew: boolean;
  private checkbitelement:boolean=true;
  constructor(private service: BitresourcesService, private renderer: Renderer2) { }
  @Input() currentViewModel:ResourceMetadata ;
  @Output() change = new EventEmitter();
  bitmetadatas:BitItem[]=[{
    bitId:1,
    id:'',
    value:''
   }];  
  data:BitItem[]=[]; 
  public ngOnInit(): void {
    if(this.currentViewModel.imageResources.length!=0){
      let igridData = this.currentViewModel.imageResources;
      for(var i=0;i<igridData.length;i++){
          this.data[i]={ bitId:i+1, id:'', value:''};
          this.data[i].id = igridData[i].id;
          this.data[i].value = igridData[i].value;
      }
      this.gridData = this.data;
  }
  else {
      this.gridData = this.bitmetadatas;
      this.data=this.bitmetadatas;
  }
  if(!isNullOrUndefined(this.gridData[0].value)){
    let results=document.getElementById("result"); 
    results.innerHTML= '<img src="' + this.gridData[0].value +'" alt="" />';
  }
    this.docClickSubscription = this.renderer.listen('document', 'click', this.onDocumentClick.bind(this));
  }

  public ngOnDestroy(): void {
      this.docClickSubscription();
      this.saveCurrentViewModel();
      this.change.emit(this.currentViewModel);
    }

  public addHandler(): void {
      this.closeEditor();

      this.formGroup = createFormGroup({
          'id': '',
          'value': ''         
      });
      this.isNew = true;

      this.grid.addRow(this.formGroup);
    }

  public removeHandler(dataItem) {
     // this.grid.;
     this.service.remove(dataItem,this.data);
     let results=document.getElementById("result"); 
        results.innerHTML= '<img src="' + "test" +'" alt="" />';
    }

  public cellClickHandler({ isEdited, dataItem, rowIndex }): void {
      if (isEdited || (this.formGroup && !this.formGroup.valid)) {
          return;
      }
      this.saveCurrent();
      
      this.formGroup = createFormGroup(dataItem);
      this.editedRowIndex = rowIndex;
      if(!isNullOrUndefined(dataItem.value)){
        let results=document.getElementById("result"); 
        results.innerHTML= '<img src="' + dataItem.value +'" alt="" />';
      }else{
        let results=document.getElementById("result"); 
        results.innerHTML= '<img src="' + "test" +'" alt="" />';
      }
      this.grid.editRow(rowIndex, this.formGroup);
    }

  public cancelHandler(): void {
      this.closeEditor();
    }

  public groupChange(groups: GroupDescriptor[]): void {
      this.groups = groups;
      this.gridData = groupBy(this.service.bitresources(), this.groups);
    }

  private closeEditor(): void {
      this.grid.closeRow(this.editedRowIndex);
      this.isNew = false;
      this.editedRowIndex = undefined;
      this.formGroup = undefined;
    }

  private onDocumentClick(e: any): void {
  
      if (this.formGroup && this.formGroup.valid &&
          !matches(e.target, '#bitGrid tbody *, #bitGrid .k-grid-toolbar .k-button')) {
          this.saveCurrent();
        }
    }

  private saveCurrent(): void {
      if (this.formGroup) {
        if(!isNullOrUndefined(this.Info)){
          this.formGroup.value.value=this.Info;
          this.Info=null;
        } 
          this.service.save(this.formGroup.value, this.isNew,this.data);
          this.closeEditor();
        }
    }
    
  public fileUploadonselect():void{
       
        var uploadsFile = <HTMLInputElement>document.getElementById("upload");
        var file = uploadsFile.files[0];
        let self = this;
        var reader = new FileReader();//这是核心！！读取操作都是由它完成的
        reader.readAsDataURL(file);        
        reader.onload=function(){                  
                    let results=document.getElementById("result"); 
                    results.innerHTML= '<img src="' + this.result +'" alt=""/>';
                    self.Info = this.result.toString();
                }
                this.Info = self.Info;
        }
  private saveCurrentViewModel() :void{
          let bititem = this.data;
          this.currentViewModel.imageResources = [];
          for(var i=0 ;i <bititem.length;i++){
            if(this.checkTextElement(bititem[i],this.currentViewModel)){
              this.bitresourceitem[i]={id:'',value:'',resourceType:ResourceType.BinaryFile}
              this.bitresourceitem[i].id = bititem[i].id;
              this.bitresourceitem[i].value = bititem[i].value;
              this.currentViewModel.imageResources.push(this.bitresourceitem[i]);
            }
            else{
              this.currentViewModel.imageResources.find(x=>(x.id==bititem[i].id)).value = bititem[i].value;
            }
          }
        }
    
        checkTextElement(element:BitItem,resourcecontent:ResourceMetadata):any{
            for(var i=0;i<resourcecontent.imageResources.length;i++){
              if(resourcecontent.imageResources[i].id==element.id){
                this.checkbitelement=false;
                break;
              }
              else this.checkbitelement=true;
            }
            return this.checkbitelement;
        }
        loadFile(){
          let resulta=document.getElementById("upload"); 
          resulta.click();
        }      
}




