export class ResourceMetadata {
    code:string;
    name:string;
    bizObject:string;
    lastModifiedTime:string;
    stringResources:TextResourceItem[];
    imageResources:BinaryResourceItem[];
}
export class TextResourceItem{
    id:string;
    value:string;
    resourceType:ResourceType;
}
export class BinaryResourceItem{
    id:string;
    value:string;
    resourceType:ResourceType;
}
export enum ResourceType{
    TextFile,
    BinaryFile
}

export class TextItem {
    textId:number;
    id:string;
    value:string;
}
export class BitItem{
    bitId:number;
    id:string;
    value:string;
}



